<template>
    <div>
        <el-button>{{ $t('message.user') }}</el-button>
        <el-button @click="changeLanguage">{{ $t('message.language') }}</el-button>
    </div>
</template>
<script lang='ts' setup>
import { ref } from 'vue'

import { useI18n } from "vue-i18n";

const { locale } = useI18n()
type Props = {
}
const props = withDefaults(defineProps<Props>(), {
})
const enOrzh = ref(false)  //默认中文
const changeLanguage = () => {
    enOrzh.value = !enOrzh.value
    locale.value = enOrzh.value ? 'en' : 'zh'
}
</script>
<style lang='scss' scoped></style>